import { useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { textToSpeech } from "./../../js/tts";
import { speechToText } from "../../js/stt";
import { positiveResponse } from "../../js/sttHandle";
import styles from "./ExResult.module.css";

function ExResult() {
  const [resText, setResText] = useState("");
  const [text, setText] = useState("");
  const [caneat, setCaneat] = useState();

  const location = useLocation();

  const navigate = useNavigate();
  const navigateTo = (path, params) => {
    navigate(path, { state: params });
    console.log("Redirecting...");
  };

  useEffect(() => {
    const setDate = () => {
      let resDate = location.state.resDate;
      resDate = resDate.replace("-", " ");
      resDate = resDate.replace("-", " ");
      resDate += "";
      console.log(resDate);
      return resDate;
    };

    const speakInfo = async () => {
      let ndate = new Date();
      const pdate = new Date(location.state.resDate);

      //can't eat : 0 / warn to eat : 1 / can eat : 2

      if (pdate > ndate) {
        const tmpdate = new Date(pdate);
        tmpdate.setDate(tmpdate.getDate() - 3);

        if (tmpdate <= ndate) {
          setCaneat(1);
          await textToSpeech(
            "   .     .",
            true
          );
        } else {
          setCaneat(2);
          await textToSpeech(" .", true);
        }
      } else {
        setCaneat(0);
        await textToSpeech("   .", true);
      }
    };

    const speakDate = async () => {
      await textToSpeech(ttsText);
      await speakInfo();
      await textToSpeech(" ?", true);
      const userRes = await speechToText(3000);
      if (positiveResponse.has(userRes)) {
        speakDate();
      } else {
        await textToSpeech("  .", 1);
        navigateTo("/home");
      }
    };

    const preventGoBack = () => {
      window.history.pushState(null, "", window.location.href);
    };

    window.history.pushState(null, "", window.location.href);
    window.addEventListener("popstate", preventGoBack);

    const resDate = setDate();
    const ttsText = `  ${resDate} .`;

    const speakTotal = async () => {
      setResText(ttsText);
      setText(resDate);
      await speakDate();
    };

    speakTotal();

    return () => {
      window.removeEventListener("popstate", preventGoBack);
    };
  }, []);

  return (
    <div>
      <h1 className={styles.description}>
          <span className={styles.textcolor}>{text}</span>
      </h1>
      <div>
        {caneat === 0 && (
          <p className={styles.nope}>   .</p>
        )}
      </div>
      <div>
        {caneat === 1 && (
          <p className={styles.warn}>
               .    
            .
          </p>
        )}
      </div>
      <div>
        {caneat === 2 && <p className={styles.caneat}> .</p>}
      </div>
    </div>
  );
}

export default ExResult;
