import { textToSpeech } from "./tts";

async function explainBtn() {
  await textToSpeech("    ", 2);
  await textToSpeech("   .", 2);
  await textToSpeech(
    "             .",
    2
  );
  await textToSpeech(
    "               .",
    2
  );
  await textToSpeech(
    "         .",
    2
  );
  await textToSpeech(
    "              3              4      . ",
    2
  );
}

async function explainApp() {
  await textToSpeech("", 2);
  await textToSpeech(
    "     29     .",
    2
  );
  await textToSpeech(
    "     29   .",
    2
  );

  await textToSpeech(
    "            .         ,                 .          .  ,            . ,             .           . ,               . ,             .              .",
    2
  );
}

export { explainBtn, explainApp };
