import { useEffect, useState } from "react";
import { textToSpeech, getSpeakRate, setSpeakRate } from "../../js/tts";
import { speechToText } from "../../js/stt";
import Button from "../Global/Button";
import styles from "./Modal.module.css";

function Modal({ setModalOpen, homeInit }) {
  const [speed, setSpeed] = useState(getSpeakRate());
  const [mode, setMode] = useState("");

  const closeModal = () => {
    setModalOpen(false);
  };

  useEffect(() => {
    const init = async () => {
      await textToSpeech(" .", 2);
      await textToSpeech(
        "   , ,    .",
        2
      );

      let currentSpeed = "";
      switch (getSpeakRate()) {
        case "1":
          currentSpeed = "";
          break;
        case "1.4":
          currentSpeed = "";
          break;
        case "2":
          currentSpeed = "";
          break;
        default:
          currentSpeed = "";
      }

      await textToSpeech("   " + currentSpeed + ".", 2);

      const getSpeedRes = async () => {
        const speedRes = await speechToText(3000);
        switch (speedRes) {
          case "":
            setSpeed("1");
            break;
          case "":
            setSpeed("1.4");
            break;
          case "":
            setSpeed("2");
            break;
          default:
            await textToSpeech("    ?", 2);
            await getSpeedRes();
        }
      };

      await getSpeedRes();

      await textToSpeech("  .", 2);
      await textToSpeech(" .", 2);
      homeInit();
      closeModal();
    };

    init();
  }, []);

  useEffect(() => {
    setSpeakRate(speed);
    console.log("speed changed to " + getSpeakRate());
  }, [speed]);

  //  onClick 
  return (
    <div className={styles.container}>
      <Button classname={styles.closeButton} text="X" />
      <h1 style={{ textAlign: "center" }}></h1>
      <div className={styles.settingBody}>
        <h4>  </h4>
        <div>
          <input
            type="radio"
            value="1"
            checked={speed === "1"}
            onChange={() => setSpeed("1")}
          />
          <label></label>
          <input
            type="radio"
            value="1.4"
            checked={speed === "1.4"}
            onChange={() => setSpeed("1.4")}
          />
          <label></label>
          <input
            type="radio"
            value="2"
            checked={speed === "2"}
            onChange={() => setSpeed("2")}
          />
          <label></label>
        </div>
        <h4> </h4>
        <div>
          <input
            type="radio"
            value=" "
            checked={mode === ""}
            onChange={() => setMode("")}
          />
          <label> </label>
          <input
            type="radio"
            value=" "
            checked={mode === ""}
            onChange={() => console.log("   .")}
          />
          <label>  ( )</label>
        </div>
      </div>
    </div>
  );
}

export default Modal;
